



#root{
  scale:100%;
}
.sheet {
  background-color: rgb(240, 239, 224);
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  display: flex;
  flex-flow: row wrap;
  margin-left: auto;
  margin-right: auto;
  scale: 95%;
  overflow: hidden;
  max-width: 1312px;
}

header{
  width: 100%;
  align-self: flex-start;
}

main{
  display: flex;
  scale: 100%;
  align-items: center;
}
.main-container{
  display: -webkit-flex;
  align-items: center;
}



footer {
  background-color: #333333;
  opacity: 1;
  height: auto;
  float: left;
  width:100%;
  display: block;
}
.footer-text{
  text-align: center;
  font-family: Poppins;
  font-size: 12px;
  color:#9F9F9F;
  padding:42px;
}
.arrow-left,.arrow-right{
  align-self: center;
  padding:25px;
  color: #bbb;
  text-decoration: none;
  font-size: 50px;
  width: 32px;
  cursor:pointer;
  position: relative;
  top: 10%;
}

.arrow-left:hover,.arrow-right:hover{
  
  color:rgb(48, 48, 48);
  transition: color 1s ease;
}
.arrow-left{
  
  order:1 ;
}

.arrow-right{
  order:3 ;
}

.dot:hover{
  background-color: rgb(48, 48, 48);
  transition: background-color 1s ease;
}

.dot{
  height:10px;
  width: 10px;
  margin:0 6px;
  background-color: #bbb;
  display: inline-block;
  transition: background-color 0.6s ease;
  border-radius: 50%;
  
}

.dot_active{
  height:10px;
  width: 10px;
  margin:0 6px;
  background-color: rgb(48, 48, 48);
  display: inline-block;
  transition: background-color 0.6s ease;
  border-radius: 50%;
}
.dotContainer{
  justify-self: center;
  margin-top: 5%;
}

ul{
  padding-left:0px;
}
li{
  list-style-type: none;
}

.school{
  font-size:15px;
  font-family: Poppins;
}


h1 {
  padding: 0 0 0 70px;
  font-family: Poppins;
  font-size: 100px;
  letter-spacing: 4px;
  overflow: hidden;
  background: linear-gradient(90deg, #393939, #959595);
background-clip: text;
-webkit-text-fill-color: rgba(255, 255, 255, 0);
 margin: 0;
}

h2{
color:#333333;
 font-family: Poppins;
 font-size: 40px;
 margin: 0;
}

h3{
  color:#333333;
  font-family: Poppins;
  font-size: 24px;
  margin-top:0;
  border-bottom:1px solid #333333;
  padding-bottom: 6px;
  width:max-content;
  margin-bottom: 0;
}

h4{
  color:#333333;
  font-family: Poppins;
  font-size: 16px;
  margin-bottom: 17px;
}

p{
  color:#333333;
  font-family: Poppins;
  font-size:13px;
  margin:0 auto;
}

.fade{
-webkit-animation-name: fade;
-webkit-animation-duration: 1s;
animation-name: fade;
animation-duration: 1s
}

@-webkit-keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

.slider{
  display: grid;
  clear: both;
  align-self: flex-end;  
  max-width: 500px;
  min-height: 280px;
}

.slide{
  height: 385px;
  width: 500px;
}

.data-container{
  align-self: flex-start;
}

.slider-container{
  order:2 ;
  overflow:hidden;
  display: flex;
  flex-flow: column wrap;
}

.data-container-birthdate{
  margin-left:39px;
}
.data-container-adress,.data-container-birthdate{
  float:left;
}
.data-container-adress,.data-container-birthdate >p{
  
  color:#333333;
  font-family: Poppins;
  font-size: 12px;
}


.avatar-box{
  display: -webkit-flex;
 margin-left: 5%;
  align-self: flex-end;
  opacity: 0.9
}

img{
  max-width:100%;
  height:auto;
}

.react-logo {
  animation: react-logo-spin infinite 20s linear;
  height: 7vmin;
}
@keyframes react-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
} 


.logo{
  height: 70px;
  width: 70px;
  padding: 10px;
  box-sizing: border-box;
  transition-duration: .5s;
}

.logo:hover{
  padding: 0px;
}

.react-logo:hover{
  padding: 0px;
}

.react-logo-active{
  animation: react-logo-spin infinite 20s linear;
  height: 70px;
}

.logoActive{
  transition-duration: .5s;
  height: 70px;
  width: 70px;
  padding:0px;
}

.formContainer{
  height: 69%;
  width:100%;
  text-align: center;
  
}

.textCont{
  width: 98%;
  height: 80px;
  resize: none;
  float:left;
}

input, textarea{
border: solid 1px #bbb;
margin-bottom: 1%;
font-family: Poppins;
color:#333333;
font-size:12px;
}

.sendButton{
padding: 10px 30px;
background-color:#919191;
border-radius: 20px;
}
.sendButton:hover{
  background-color:#333333 ;
  color:#919191;
  transition: background-color 1s ease;
 
}

.email{
  float:left;
}

.skillsDescription{
  margin-top:10px;
}

.navIcons{
  padding: 10px 0 10px 0;
   width: 500px;
}

.iconDetailsInfo{
 
 color:#919191
}


@media (max-width: 340px){
  body{
    margin:1px;
  }

  .data-container-birthdate{
    margin-left: auto;
  }

  main{
    display: grid;
    justify-content: space-around;
    
  }

  .main-slider{
    display: flex;
    max-width: 100%;
    max-height: 100%;
  }

  .avatar-box{
    display: none;

  }

  h3{
    width: 100vw;
  }

  .sheet{
    padding: 5px;
    scale: 99%;
  }

  .slider-container{

    overflow:hidden;
    order: 2;
    display: flex;
    flex-direction: column;
    position: relative;
  }

  .slider{
    width: 100vw;
    align-self: flex-start;
    max-width: 100vw;
  }

  .slide{
    height: auto;
    width: 90vw;
  }

  .dotContainer{
    position: sticky;
    justify-self: center ;
    margin-top: 2% ;
    margin-bottom: 3% ;
  }

  .arrow-left, .arrow-right{
  display:none;
  }

  .slide-skills-container{
    display: flex;
    flex-flow: row wrap;
  }

}

